import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { HomeResource } from '../../resources/home-resource';

@Component({
  selector: 'top-app-nav-menu',
  templateUrl: './top-nav-menu.component.html',
  styleUrls: ['./top-nav-menu.component.css']
})
export class TopNavMenuComponent implements OnInit {

  HomeResource = HomeResource;
  isLoggedIn:boolean = false;

  isExpanded = false;
  constructor(private authService: AuthService) {
    this.authService.isLoggedInObs().toPromise().then(x=> this.isLoggedIn= x.valueOf());
  }

  ngOnInit(){
    this.authService.isLoggedInObs().toPromise().then(x=> this.isLoggedIn= x.valueOf());
  }

  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }

  startSignoutMainWindow() {
    this.authService.startSignout();
  }
}
