import { Injectable } from '@angular/core';
import { UserManager, UserManagerSettings, User } from 'oidc-client';
import { environment } from '../../environments/environment';
import { from as fromPromise, Observable, observable,of as observableOf} from 'rxjs';
import { map, filter, catchError, mergeMap } from 'rxjs/operators';



@Injectable()
export class AuthService {
  private manager = new UserManager(getClientSettings());
  private user: User = null;
  constructor() {
    this.manager.getUser().then(user => {
      this.user = user;
    });
  }

  isLoggedIn(): boolean {
    return this.user != null && !this.user.expired;
  }

  isLoggedInObs(): Observable<boolean> {
       return fromPromise(this.manager.getUser()).pipe(
          map(user => {
            if (user) {
              return true;
            } else {
              return false;
            }
          })
        );
  }

  /*return Observable.fromPromise(this.mgr.getUser()).map<User, boolean>((user) => {
    if (user) {
      return true;
    } else {
      return false;
    }
  });*/


  getClaims(): any {
    return this.user.profile;
  }

  getAuthorizationHeaderValue(): string {
    return `${this.user.token_type} ${this.user.access_token}`;
  }

  startAuthentication(): Promise<void> {
    return this.manager.signinRedirect();
  }

  completeAuthentication(): Promise<void> {
    return this.manager.signinRedirectCallback().then(user => {
      this.user = user;
    });
  }

  startSignout(){
    this.manager.getUser().then(user => {
      return this.manager.signoutRedirect({ id_token_hint: user.id_token }).then(resp => {
        console.log('signed out', resp);
      }).catch(function (err) {
        console.log(err);
      });
    });   
  };

  endSignout() {
    this.manager.signoutRedirectCallback().then(function (resp) {
      console.log('signed out', resp);
    }).catch(function (err) {
      console.log(err);
    });
  };
}

export function getClientSettings(): UserManagerSettings {
  return {
      authority: environment.OuthSettings.authority,
      client_id: environment.OuthSettings.client_id,
      redirect_uri: environment.OuthSettings.redirect_uri,
      post_logout_redirect_uri:environment.OuthSettings.post_logout_redirect_uri,
      response_type:environment.OuthSettings.response_type,
      scope:environment.OuthSettings.scope,
      filterProtocolClaims: environment.OuthSettings.filterProtocolClaims,
      loadUserInfo: environment.OuthSettings.loadUserInfo,
      automaticSilentRenew: environment.OuthSettings.automaticSilentRenew,
      silent_redirect_uri: environment.OuthSettings.silent_redirect_uri
  };
}
