import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpRequest, HttpEvent, HttpEventType, HttpHeaders } from '@angular/common/http';
import { AuthService } from '../services/auth.service';
import {environment} from '../../environments/environment';

@Component({
  selector: 'app-call-api',
  templateUrl: './call-api.component.html',
  styleUrls: ['./call-api.component.css']
})
export class CallApiComponent implements OnInit {
  response: string;
  constructor(private http: HttpClient, private authService: AuthService) { }
  ngOnInit() {
    let headers = new HttpHeaders({'Authorization': this.authService.getAuthorizationHeaderValue()});
    this.http.get(environment.apiURL, {headers:headers})
      .subscribe(response => this.response = response.toString(), error => console.error(error));
  }
}
